@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans+Thai:wght@200;400;500;600&display=swap);
*{
  box-sizing: border-box;
  margin:0;
  font-family: 'IBM Plex Sans Thai', sans-serif;
  font-size:16px;
}
body{
  background:#1c1f2e;
}
.coinapp{
  background-color:#1c1f2e;
  color:white;
  display:flex;
  flex-direction: column;
  align-items: center;
}
.coinapp__name{
  color:#d6dfec;
  font-size:2rem;
  text-transform: capitalize;
}
.loader{
  display:flex;
  justify-content: center;
}
.coin{
    background-color: #1f2337;
    box-shadow: 0 4px 8px 0 rgb(0 0 0 / 20%), 0 6px 20px 0 rgb(0 0 0 / 19%);
    border-radius: 20px;
    margin: 2em auto;
    padding: 1em;
    width: 90%;
    font-size:1.5rem;
    font-weight: 500;
}
.coin__data{
    background: #262a3b;;
    color: #a0a0ae;
    padding: .5em 1em;
    border-radius: 10px;
}
.coin-price{text-transform: uppercase;}

.coin__priceChange{
    background: #0d0f32;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: .5em 1.25em;
    margin-top: 1em;
    border-radius: 20px;
}
.coin__priceChange--green,.coin__priceChange--red{
    font-weight: 700;
    font-size: 1.25rem;
}
.coin__priceChange--green{
    color:#0cdc2c;
}
.coin__priceChange--red{
    color: #d11515;

}
.coin__name{
    font-size:1.75rem;
    font-weight:700;
    color:#be8c5c;
}
.coinsearch__input{
    background: #C33764;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #1D2671, #C33764); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    outline: none;
    border:none;
    border-radius: 5px;
    margin-top:1em;
    padding:.5em;
    color:white;
}
.coinsearch__input::-webkit-input-placeholder{
    color:#8894a6;
}
.coinsearch__input:-ms-input-placeholder{
    color:#8894a6;
}
.coinsearch__input::placeholder{
    color:#8894a6;
}

.currencyDropdown__select{
    background: #1c1f2e;
    color: white;
    outline: none;
    text-transform: uppercase;
    margin-top: 3em;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

